 * ========================================================================== */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: calc(16px + (24 - 16) * (100vw - 320px) / (1400 - 320));
  height: 100vh;
}

body {
  margin: 0 auto;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Hero {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 0;
}

.Hero__background {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  margin: 0;
  height: 90vh;
  width: 100%;
}

.Hero__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: black;
  line-height: 1.5;
  letter-spacing: 2px;
}

.Hero__title {
  margin-top: 0;
  font-size: 3rem;
}

.Hero__subtitle {
  font-size: 2rem;
}

.Header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  margin: 0 auto;
  background-color: white;
  max-width: 1400px;
}

.Header__title {
  margin-left: 2rem;
  color: black;
  font-size: 2rem;
}

.Header__links {
  display: flex;
  align-items: center;
  margin-right: 2rem;
  height: 100%;
}

.Header__links ul {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  height: 100%;
}

.Header__links ul li {
  display: flex;
  align-items: center;
  height: 100%;
  margin: 0 0.8rem;
}

.Header__links ul li a {
  display: flex;
  align-items: center;
  font-family: 'Caveat';
  text-decoration: none;
  color: black;
  font-size: 1.2rem;
  cursor: pointer;
}

.active {
  border-bottom: 3px solid rgb(0, 0, 0);
  margin-top: 3px;
}

.Header__links ul li:last-child {
  margin-right: 0;
}

.Header__logo {
  font-family: 'Caveat';
}


.AboutContentRow {
  display: flex;
  flex-direction: column;
  background-color: rgb(46, 46, 46);
  border-radius: 10px;
  color: white;
  text-align: center;
  padding: 0.8rem;
  margin: 1rem auto;
}

.row_image_container {
  width: 100%;
}

.row_image {
  display: block;
  width: 90%;
  max-width: 512px;
  margin: 0 auto;
  padding: 1.5rem 0;
  border-radius: 50%;
}

.row_text {
  margin: 0.8rem;
}

@media all and (min-width: 1000px) {
  .AboutContentRow {
    flex-direction: row;
    align-items: center;
  }

  .reverse {
    flex-direction: row-reverse;
  }

  .row_image_container {
    width: 50%;
  }

  .row_content {
    width: 50%;
    font-size: 0.9rem;
  }
}

.About {
  min-height: 100vh;
  background-color: black;
  padding: 10vh 2rem 2rem 2rem;
}

.About__header {
  color: white;
  text-align: center;
  font-size: 2rem;
  margin: 1rem 1rem 5vh 1rem;
}
.ProjectScreen {
  display: inline-block;
  position: absolute;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  background-color: rgb(46, 46, 46);
}

.ProjectScreen__image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 63%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center top;
  padding: 0 .5rem;
}

.ProjectScreen__icon {
  display: block;
  color: rgb(0, 0, 0);
  font-size: 1.5rem;
  border: none;
  background: none;
}

.ProjectScreen__content {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100vh;
  color: white;
  font-size: 0.75rem;
  padding: calc(95vh * 0.37) 1rem 1rem 1rem;
  text-align: center;
  line-height: 1.2;
}

.content__desc_container {
  padding-bottom: 0.75rem;
}

.content__links_container {
  display: flex;
  justify-content: space-evenly;
  padding-bottom: .75rem;
}

.content__title {
  font-size: 1.3rem;
  margin-bottom: 0.75rem;
}

.content__next {
  background: none;
  border: none;
  color: white;
  padding: 15px 0 0 0;
  font-size: 1.3rem;
  cursor: pointer;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
}

.content__links {
  color: white;
  font-weight: 600;
}

.content__stack p:last-child {
  padding-top: 0.5rem;
}

.content__stack_header {
  padding-bottom: 0.5rem;
  font-weight: 600;
}

.content__stack_list_title {
  font-weight: 600;
}

@media all and (min-width: 667px) {
  .image_arrow {
    color: white;
  }

  .ProjectScreen__icon {
    padding: 0 1rem;
  }
}

@media all and (min-width: 100px) {
  .ProjectScreen__icon {
    padding: 0 4rem;
  }
}
.ProjectSlider {
  position: relative;
  min-height: 100vh;
  overflow-x: hidden;
}

.Projects {
  min-height: 100vh;
  background-color: white;
  padding: 10vh 0 2rem 0;
}

.Projects__header {
  color: black;
  text-align: center;
  font-size: 2rem;
  margin: 1rem 1rem 5vh 1rem;
}
.Contact {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  min-height: 100vh;
  background-color: black;
  color: white;
  padding: 10vh 2rem 2rem 2rem;
  font-size: 1rem;
  text-align: center;
}

.Contact__header {
  text-align: center;
  font-size: 2rem;
  margin: 1rem 1rem 5vh 1rem;
}

.Contact__info {
  padding-top: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 75%;
  max-width: 300px;
  margin: 0 auto 0 auto;
}

.Contact__icon {
  color: white;
}

.Contact__link {
  display:flex;
  justify-content: space-between;
  text-decoration: none;
  width: 50%;
  color: white;
}

.link_text {
  display: inline-block;
}
header {
  position: fixed;
  top: 0;
  left:0;
  right: 0;
  height: 10vh;
  background-color: white;
  z-index: 10;
  box-shadow: 0px 0px 10px 3px rgba(75, 75, 75, 0.3);
}

section {
  max-width: 1400px;
  margin: 0 auto;
}

.headerWrapper {
  width: 100%;
  background-color: white;
}

.aboutWrapper {
  width: 100%;
  background-color: black;
}

.projectWrapper {
  width: 100%;
  background-color: white;
}

.contactWrapper {
  width: 100%;
  background-color: black;
}
